import axios from "./http";

//
export const getFundRank = (data) => {
  return axios({
    method: "post",
    url: "/fund/rank",
    data,
  });
};

//
export const getBaseInfo = (code) => {
  return axios({
    method: "get",
    url: "/fund?code=" + code,
  });
};

//
export const getFundDetail = (data) => {
  return axios({
    method: "get",
    url: "/fund/detail",
    params: data,
  });
};

//
export const getFundPosition = (code) => {
  return axios({
    method: "get",
    url: "/fund/position?code=" + code,
  });
};
